    isMatch: string;
    cameraName: string;
    cameraId: string;
}

interface ITableApiParam {
    paging: {
        page: number;
        pageSize: number;
    };
    sorting?: {
        field?: string;
        order?: number;
    };
}

interface ISelectOptionItem {
    personGroups: IComponent.ISelectionBase[];
    camera: IComponent.ISelectionBase[];
}

// form data
interface IFormItem {
    isClearState: boolean;
    isSearch: boolean;
}

interface IFormData {
    objectId: string;
    imageSrc: string;
    date: string;
    time: string;
