    }

    private async pageToPreview(rowData: ITableData): Promise<void> {
        this.pageItem.page = EPageStep.form;
        this.pageItem.action = EPageAction.preview;

        await this.formConfigSet(rowData);
    }

    //////////////////// Init Table ////////////////////
    private async initTable(): Promise<void> {
        this.initTableColumns();
        this.initTableParam();
        await this.tableReload(this.tableParam);
    }

    private initTableColumns(): void {
        this.tableItem.columns = [
            { title: this.$i18n.Common_NO, type: ETableRowType.autoIndex },
            { title: this.$i18n.Date_Date, key: 'date', sort: true },
            { title: this.$i18n.Date_Time, key: 'time' },
            { title: this.$i18n.Common_EmployeeId, key: 'personEmployeeId', sort: true },
            { title: this.$i18n.Common_Name, key: 'personName', sort: true },
            { title: this.$i18n.Common_PersonGroup, key: 'personGroup', type: ETableRowType.any },
            { title: this.$i18n.Common_Email, key: 'personEmail', sort: true, type: ETableRowType.any },
            { title: this.$i18n.Common_Photo, key: 'snapshotSrc', type: ETableRowType.image },
            { title: this.$i18n.Table_Type_Is_Match, key: 'isMatch' },
            { title: this.$i18n.Table_Type_Camera_Name, key: 'cameraName' },
        ];
    }

    private initTableParam() {
        this.tableParam = {
            page: this.tableItem.paging.page,
            pageSize: this.tableItem.paging.pageSize,
            sortingField: this.tableItem.sorting && this.tableItem.sorting.field ? this.tableItem.sorting.field : '',
            sortingOrderEnum: this.tableItem.sorting && this.tableItem.sorting.orderEnum ? this.tableItem.sorting.orderEnum : ETableSorting.none,
            sortingOrder: this.getSortingOrderByEnum(this.tableItem?.sorting?.orderEnum),
        };
    }

    private getSortingOrderByEnum(orderEnum: ETableSorting): number {
        switch (orderEnum) {
            case ETableSorting.asc:
                return 0;
            case ETableSorting.desc:
                return 1;
            case ETableSorting.none:
            default:
                return undefined;
        }
    }

