        this.formConfig.email.label = this.$i18n.Common_Email;
        this.formConfig.email.placeholder = this.$i18n.Placeholder_Email;
        this.formConfig.email.formType = EFormType.text;

        // personGroupsView
        this.formConfig.personGroupsView.id = 'personGroupsView';
        this.formConfig.personGroupsView.label = 'Person Groups';
        this.formConfig.personGroupsView.placeholder = 'Person Groups';
        this.formConfig.personGroupsView.formType = EFormType.text;

        // isMatch
        this.formConfig.isMatch.id = 'isMatch';
        this.formConfig.isMatch.label = 'Is Match';
        this.formConfig.isMatch.placeholder = 'Is Match';
        this.formConfig.isMatch.formType = EFormType.text;

