    }

    //////////////////// Table ////////////////////
    private async tableGetApiData(tableParam: IComponent.ITableParam): Promise<void> {
        if (tableParam && tableParam.page && tableParam.pageSize) {
            this.tableItem.paging.page = tableParam.page ? tableParam.page : this.tableItem.paging.page;
            this.tableItem.paging.pageSize = tableParam.pageSize ? tableParam.pageSize : this.tableItem.paging.pageSize;

            this.tableApiParam.paging = {
                page: this.tableItem.paging.page,
                pageSize: this.tableItem.paging.pageSize,
            };
        }

        if (tableParam && tableParam.sortingField && tableParam.sortingOrder !== undefined) {
            this.tableApiParam.sorting = {
                field: tableParam.sortingField,
                order: tableParam.sortingOrder,
            };
        } else {
            delete this.tableApiParam.sorting;
        }

        this.loading.show();

        let data = this.getFakeData(tableParam);
        this.tableSetData(data);

        this.loading.hide();
    }

    private getFakeData(tableParam: IComponent.ITableParam): any {
        let data: any[] = JSON.parse(JSON.stringify(TABLE_GET_DATA));

        // sorting
        if (!!tableParam && !!tableParam.sortingField && tableParam.sortingOrderEnum !== ETableSorting.none) {
            let sortingField: string = tableParam.sortingField;
            data = data.sort((a, b) => {
                if (tableParam.sortingOrder === 0) {
                    if (typeof a[sortingField] === 'number') {
                        return a[sortingField] - b[sortingField];
                    }

                    a[sortingField] = a[sortingField] || '';
                    b[sortingField] = b[sortingField] || '';
                    if (a[sortingField] > b[sortingField]) return 1;
                    if (a[sortingField] < b[sortingField]) return -1;
                } else {
                    if (typeof a[sortingField] === 'number') {
                        return b[sortingField] - a[sortingField];
                    }

                    a[sortingField] = a[sortingField] || '';
                    b[sortingField] = b[sortingField] || '';
                    if (a[sortingField] > b[sortingField]) return -1;
                    if (a[sortingField] < b[sortingField]) return 1;
                }

                return 0;
            });
        }

        // pagination
        let pageSize: number = tableParam?.pageSize || this.tableApiParam.paging.pageSize;
        let total: number = data.length;
        let totalPages: number = Math.ceil(total / pageSize);
        let page: number = tableParam?.page || this.tableApiParam.paging.page;
        if (page > totalPages) {
            page = totalPages || 1;
        }

        this.tableItem.paging.pageSize = pageSize;
        this.tableItem.paging.page = page;
        this.tableItem.paging.totalPages = totalPages;
        this.tableItem.paging.total = total;

        data = data.slice((page - 1) * pageSize, page * pageSize);

        return data;
    }

    private async tableReload(tableParam: IComponent.ITableParam): Promise<void> {
        if (tableParam) {
            this.tableParam.page = tableParam.page ? tableParam.page : this.tableItem.paging.page;
            this.tableParam.pageSize = tableParam.pageSize ? tableParam.pageSize : this.tableItem.paging.pageSize;
            this.tableParam.sortingField = tableParam.sortingField;
            this.tableParam.sortingOrder = tableParam.sortingOrder;
            await this.tableGetApiData(this.tableParam);
        }
    }

    private tableSetData(result: any[]): void {
        let tableData: ITableData[] = [];

        (result || []).forEach((element) => {
            tableData.push({
                objectId: element.objectId ? element.objectId : '',
                date: ServiceResolveText.resolveDate(element.date),
                time: ServiceResolveText.resolveDate(element.date, EDatetimeFormat.timeHHmm),
                personEmployeeId: element.personEmployeeId ? element.personEmployeeId : '',
                personName: element.personName ? element.personName : '',
                personGroup: element.personGroups ? element.personGroups : [],
                personEmail: element.personEmail ? element.personEmail : '',
                snapshotSrc: ServiceResolveText.resolvePhotoIsUrlOrBase64(element.snapshotSrc),
                isMatch: ServiceResolveText.resolveBooleanToString(element.isMatch, this, 'match', 'not match'),
                cameraName: element.cameraName ? element.cameraName : '',
                cameraId: element.cameraId ? element.cameraId : '',
            });
        });

        this.tableItem.datas = tableData;
    }

    //////////////////// Form Show ////////////////////
    private formTitle(title: string): string {
        return title ? title : this.$i18n.Form_FilterCreateNewData;
    }

    private formPreview(): boolean {
        return this.pageItem.action === EPageAction.preview;
    }

    //////////////////// Form Set Data ////////////////////

    private formDataClear(): void {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));
    }

    private async formConfigSet(data: any = undefined): Promise<void> {
        if (data) await this.formDataSet(data);
        this.formConfig.imageSrc.src = this.formData.imageSrc;
        this.formConfig.imageSrc.orignalSrc = this.formData.imageSrc;
    }

    private async formDataSet(data: ITableData): Promise<void> {
        // view
        this.formData.objectId = data.objectId ? data.objectId : '';
        this.formData.date = data.date ? data.date : '';
        this.formData.time = data.time ? data.time : '';
        this.formData.objectId = data.objectId ? data.objectId : '';
        this.formData.name = data.personName ? data.personName : '';
        this.formData.employeeId = data.personEmployeeId ? data.personEmployeeId : '';
        this.formData.email = data.personEmail ? data.personEmail : '';
        this.formData.camera = data.cameraName ? data.cameraName : '';
        this.formData.isMatch = data.isMatch ? data.isMatch : '';

        this.formData.personGroupsView = data.personGroup.map((item) => item.name).join(', ');

        if (data.snapshotSrc && ServiceBase64.isBase64Image(data.snapshotSrc)) {
            this.formData.imageSrc = data.snapshotSrc;
        } else if (data.snapshotSrc) {
            this.formData.imageSrc = await ServiceBase64.urlToBase64(data.snapshotSrc);
        }
    }
}

